
.garis {
      display: block;
      width: 45px;
      height: 5px;
      background: #8bc751;
      margin-bottom: 20px;
}
.modal-co {
      width: 519px;
      background-color: #ffffff !important;
      box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2) !important;
      /* width: 100% !important; */
      /* margin: 100px auto; */
      padding: 50px 50px;
      background-color: #fff;
      border-radius: 2px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
      transition: all .3s ease;
      margin: 0 auto;
      border-radius: 5px;
}
.kotak {
      padding: 6px;
      margin-bottom: 10px;
      border-color: #eaeaea;
      border-style: solid;
      border: 2px solid #eaeaea;
}
.calender-div {
      z-index: 999;
}
@import '../../modal.css';



