
.mloading-mask[data-v-69795cd9] {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}
.mloading-wrapper[data-v-69795cd9] {
  display: table-cell;
  vertical-align: middle;
}
.mloading-container[data-v-69795cd9] {
  width: 250px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 2px;
  /*box-shadow: 0 2px 8px rgba(0, 0, 0, .33);*/
  transition: all .3s ease;
}
.mloading-header h3[data-v-69795cd9] {
  margin-top: 0;
  color: #42b983;
}
.mloading-body[data-v-69795cd9] {
  margin: 20px 0;
}
.mloading-default-button[data-v-69795cd9] {
  float: right;
}

/*
 * The following styles are auto-applied to elements with
 * transition="mloading" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the mloading transition by editing
 * these styles.
 */
.mloading-enter[data-v-69795cd9] {
  opacity: 0;
}
.mloading-leave-active[data-v-69795cd9] {
  opacity: 0;
}
.mloading-enter .mloading-container[data-v-69795cd9],
.mloading-leave-active .mloading-container[data-v-69795cd9] {
  transform: scale(1.1);
}




